import React from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import TwitterText from 'twitter-text'
import twitterDateFormat from '../utils/TwitterDateFormat'
import Masonry from 'react-masonry-component'
import Layout from "../components/layout"
import SEO from '../components/SEO'

function IndexPage () {
const { allTwitterSearchTweetsBuildingMfe: data } = useStaticQuery(
    graphql`
      query {
        allTwitterSearchTweetsBuildingMfe{
          edges {
            node {
              id_str
              created_at
              full_text
              user {
                screen_name
              }
              entities {
                media {
                  media_url
                }
              }
            }
          }
        }
      }
    `
  )

  /*
  
  filter data produced only by me + change hashtag to search:
  allTwitterSearchTweetsBuildingMfe (
          
    filter: {
              user: {
                screen_name: {eq: "lucamezzalira"}
              }
              }
  ) 

  */ 

  const ShowImage = (props) => {
    if(props.url.media){
      return <img src={props.url.media[0].media_url} alt="pic from twitter"/>
    }else{
      return ""
    }
  }

  return (
    <Layout>
     <SEO title="#BuildingMFE"></SEO>
     <Masonry className="showcase">
       {data.edges
         .map(({ node }) => node)
         .map(({ full_text, id_str, user, created_at, entities }) => (
        <div key={id_str} className="showcase__item">
          <figure className="card">
            <figcaption className="card__caption">
            <ShowImage url={entities}/>            
              <h6 className="card__title">
                {twitterDateFormat(created_at)}
              </h6>
              <div className="card__description" dangerouslySetInnerHTML={{__html: TwitterText.autoLink(full_text, {targetBlank: true})}}></div>
              <a className="card__external_link" href={`https://twitter.com/${user.screen_name}/status/${id_str}`} target="_blank">View on Twitter</a>
            </figcaption>
          </figure>
        </div>
      ))}
    </Masonry>
  </Layout>
  )

}

export default IndexPage

