const twitterDateFormat = (time) => {
    var months = [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec"
    ]

    let d = new Date(time);
    return d.getDate() + ' ' + months[d.getMonth()] + ' ' + d.getFullYear();

  }

export default twitterDateFormat